<template>
  <!-- modal login-->
  <b-modal
    id="modal-aprobar-servicios"
    ref="refModalAprobarServicios"
    v-model="dialog"
    centered
    :title="formTitle"
    :hide-footer="true"
    size="xl"
    no-close-on-backdrop
    no-close-on-esc
    hide-header-close
  >
    <b-form @submit.prevent="onSubmit">
      <b-row class="d-flex justify-content-center">
        <h4 class="text-uppercase font-weight-bold text-primary">
          {{ triajeData.persona.nombre_completo }}
        </h4>
      </b-row>
      <b-row class="d-flex justify-content-center">
        <strong><i>DNI: </i></strong>
        <p class="font-weight-bold font-italic font-weight-bold">
          {{ triajeData.persona.numero_documento }}
        </p>
      </b-row>
      <b-row>
        <b-col md="4">
          <b-media
            no-body
            class="mb-1"
          >
            <b-media-aside
              class="mr-2"
            >
              <b-avatar
                size="30"
                variant="light-info"
              >
                <feather-icon
                  size="16"
                  icon="CalendarIcon"
                />
              </b-avatar>
            </b-media-aside>
            <b-media-body class="my-auto">
              <h6 class="font-weight-bolder mb-0">
                Desde Cita
              </h6>
            </b-media-body>
          </b-media>
          <b-table-simple
            striped
            bordered
            borderless
            small
            responsive
          >
            <b-thead head-variant="dark">
              <b-tr>
                <b-th variant="secondary">
                  N
                </b-th>
                <b-th variant="primary">
                  Servicio
                </b-th>
                <b-th>Accin</b-th>
              </b-tr>
            </b-thead>
            <b-tbody>
              <b-tr
                v-for="(item, index) of serviciosCitas"
                :key="index"
              >
                <b-td>{{ index+1 }}</b-td>
                <b-td>
                  {{ item.nombre }}
                </b-td>
                <b-td>
                  <b-button
                    :variant="item.pivot.estado === 'aprobado' ? 'success' : 'danger'"
                    size="sm"
                    :value="item.pivot.estado"
                    @click="crearFUACitas(item)"
                  >
                    {{ item.pivot.estado === 'aprobado' ? 'Aprobad' : 'Por Aprobar' }}
                  </b-button>
                </b-td>
              </b-tr>
            </b-tbody>
          </b-table-simple>
        </b-col>

        <!-- Triaje servicios  -->
        <b-col md="4">
          <b-media
            no-body
            class="mb-1"
          >
            <b-media-aside

              class="mr-2"
            >
              <b-avatar
                size="30"
                variant="light-danger"
              >
                <feather-icon
                  size="16"
                  icon="HeartIcon"
                />
              </b-avatar>
            </b-media-aside>
            <b-media-body class="my-auto">
              <h6 class="font-weight-bolder mb-0">
                Desde Triaje
              </h6>
            </b-media-body>
          </b-media>

          <b-table-simple
            striped
            bordered
            borderless
            small
            responsive
          >
            <b-thead head-variant="dark">
              <b-tr>
                <b-th>N</b-th>
                <b-th>Servicio</b-th>
                <b-th>Accin</b-th>
              </b-tr>
            </b-thead>
            <b-tbody>
              <b-tr
                v-for="(item, index) of serviciosTriaje"
                :key="index"
              >
                <b-td>{{ index+1 }}</b-td>
                <b-td>{{ item.nombre }}</b-td>
                <b-td>
                  <b-button
                    :variant="item.pivot.estado === 'aprobado' ? 'success' : 'danger'"
                    size="sm"
                    :value="item.pivot.estado"
                    @click="crearFUATriaje(item)"
                  >
                    {{ item.pivot.estado === 'aprobado' ? 'Aprobad' : 'Por Aprobar' }}
                  </b-button>
                </b-td>
              </b-tr>
            </b-tbody>
          </b-table-simple>
        </b-col>

        <b-col md="4">
          <b-media
            no-body
            class="mb-1"
          >
            <b-media-aside
              class="mr-2"
            >
              <b-avatar
                size="30"
                variant="light-warning"
              >
                <feather-icon
                  size="16"
                  icon="ChevronsRightIcon"
                />
              </b-avatar>
            </b-media-aside>
            <b-media-body class="my-auto">
              <h6 class="font-weight-bolder mb-0">
                Para Interconsulta
              </h6>
            </b-media-body>
          </b-media>

          <b-table-simple
            striped
            bordered
            borderless
            small
            responsive
          >
            <b-thead head-variant="dark">
              <b-tr>
                <b-th>N</b-th>
                <b-th>Servicio</b-th>
                <b-th>Accin</b-th>
              </b-tr>
            </b-thead>
            <b-tbody>
              <b-tr
                v-for="(item, index) of serviciosInter"
                :key="index"
              >
                <b-td>{{ index+1 }}</b-td>
                <b-td>{{ item.nombre }}</b-td>
                <b-td>
                  <b-button
                    :variant="item.pivot.estado === 'aprobado' ? 'success' : 'danger'"
                    size="sm"
                    :value="item.pivot.estado"
                    @click="crearFUAInterconsulta(item)"
                  >
                    {{ item.pivot.estado === 'aprobado' ? 'Aprobad' : 'Por Aprobar' }}
                  </b-button>
                </b-td>
              </b-tr>
            </b-tbody>
          </b-table-simple>
        </b-col>
      </b-row>

      <hr class="my-1 py-1">

      <b-row>
        <b-col cols="12">
          <!-- Input servicio -->
          <b-form-group
            label="Adicionar Servicios"
          >
            <v-select
              v-model="triajeData.servicios"
              :dir="$store.state.appConfig.isRTL ? 'rtl' : 'ltr'"
              label="nombre"
              :options="optionsTipos"
              :reduce="val => val.id"
              multiple
              placeholder="Seleccionar"
              @input="agregarServicios"
            />
          </b-form-group>
        </b-col>
      </b-row>
      <b-row>
        <b-col md="6">
          <b-form-group label="Celular para contacto">
            <b-form-input
              v-model="triajeData.celular"
              type="text"
              required
            />
          </b-form-group>
        </b-col>

        <b-col md="6">
          <b-form-group label="Fecha de la cita">
            <flat-pickr
              v-model="triajeData.fecha_cita"
              :config="config"
              class="form-control"
            />
          </b-form-group>
        </b-col>
      </b-row>

      <b-form-group label="Motivo:">
        <b-form-textarea
          v-model="triajeData.motivo"
          placeholder="Describir motivo..."
          rows="3"
          required
          max-rows="6"
        />
      </b-form-group>

      <b-row class="mt-1">
        <b-col>
          <!-- <b-button
            variant="success"
            class="mb-1 mb-sm-0 mr-0 mr-sm-1"
            :block="$store.getters['app/currentBreakPoint'] === 'xs'"
            type="submit"
          >
            Guardar
          </b-button> -->
          <b-button
            variant="outline-secondary"
            class="mb-1 mb-sm-0 mr-0 mr-sm-1"
            :block="$store.getters['app/currentBreakPoint'] === 'xs'"
            @click="close"
          >
            Cerrar
          </b-button>
          <!-- <b-button
            variant="danger"
            :block="$store.getters['app/currentBreakPoint'] === 'xs'"
            @click="aprobarCita"
          >
            Aprobar
          </b-button> -->
        </b-col>
      </b-row>
    </b-form>
  </b-modal>

</template>

<script>
import {
  BMedia, BAvatar, BMediaBody, BMediaAside, BTableSimple, BThead, BTbody, BTr, BTd, BTh, BRow, BCol, BForm, BModal, BFormGroup, BFormInput, BFormTextarea, BButton,
} from 'bootstrap-vue'
import flatPickr from 'vue-flatpickr-component'
// eslint-disable-next-line import/extensions
// eslint-disable-next-line import/no-extraneous-dependencies
import { Spanish } from 'flatpickr/dist/l10n/es'
import vSelect from 'vue-select'
import Ripple from 'vue-ripple-directive'
import {
  ref, watch, toRefs, nextTick, computed,
} from '@vue/composition-api'
import store from '@/store'
// Notification
import { useToast } from 'vue-toastification/composition'
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'
import axios from '@axios'

export default {
  components: {
    BRow,
    BCol,
    BModal,
    BForm,
    BFormGroup,
    BFormInput,
    BFormTextarea,
    BButton,
    BTableSimple,
    BThead,
    BTbody,
    BTr,
    BTd,
    BTh,
    BMedia,
    BAvatar,
    BMediaBody,
    BMediaAside,

    // Externos
    flatPickr,
    vSelect,
  },
  directives: {
    Ripple,
  },
  props: {
    editedItem: {
      type: Object,
      default() {
        return { }
      },
    },
  },
  data() {
    return {
      // Spanish,
      config: {
        static: true,
        // inline: true,
        // wrap: true, // set wrap to true only when using 'input-group'
        // allowInput: true,
        enableTime: true,
        // dateFormat: 'F j, Y h:i K',
        dateFormat: 'Y-m-d H:i',
        altFormat: 'd/m/Y h:i K',
        altInput: true,
        locale: Spanish, // locale for this instance only
        // time_24hr: true,
      },
      selected: null,
      selectedEstado: null,
      optionsTipos: [],
      optionsEstado: [{ id: 1, nombre: 'aprobado' }, { id: 2, nombre: 'no aprobado' }, { id: 3, nombre: 'cancelado' }],
    }
  },
  created() {
    this.$http.get('servicios/tipos').then(response => {
      this.optionsTipos = response.data.data
    })
  },
  setup(props, { emit }) {
    // const { persona } = toRefs(props)
    const { editedItem } = toRefs(props)

    const editedIndex = ref(-1)
    const dialog = ref(false)
    const toast = useToast()

    const serviciosCitas = ref([])
    const serviciosTriaje = ref([])
    const serviciosInter = ref([])

    const blankTriajeData = {
      id: null,
      paciente_id: null,
      servidor_publico: null,
      motivo: null,
      celular: null,
      estado: 'no aprobado',
      fecha_cita: null,
      servicios: [],
      persona: {
        nombre_completo: null,
        numero_documento: null,
      },
    }

    const blankAtencion = {
      paciente_id: null,
      servidor_publico: null,
      triaje_id: null,
      servicio_id: null,
    }

    const atencionData = ref(JSON.parse(JSON.stringify(blankAtencion)))

    const triajeData = ref(JSON.parse(JSON.stringify(blankTriajeData)))

    const resetuserData = () => {
      triajeData.value = JSON.parse(JSON.stringify(blankTriajeData))
    }

    const close = () => {
      dialog.value = false
      nextTick(() => {
        resetuserData()
        editedIndex.value = -1
      })
    }

    const formTitle = computed(() => (editedIndex.value === -1 ? 'Creacin de FUAs' : `Agregar y/o quitar FUAs - Triaje N ${editedIndex.value}`))

    watch(dialog, () => {
      // eslint-disable-next-line no-unused-expressions
      dialog.value || close()
    })

    watch(editedItem, () => {
      editedIndex.value = editedItem.value.id
      // Datos de triaje
      triajeData.value = editedItem.value
      serviciosCitas.value = editedItem.value.cita_id ? editedItem.value.cita.servicios : []
      serviciosTriaje.value = editedItem.value.servicios.length ? editedItem.value.servicios : []

      // Obtenemos todas las atenciones que al menos tengan un servicio asignado que seria una interconsulta
      const atencionesServicios = editedItem.value.atencion.filter(item => item.servicios.length > 0)

      // Debemos almacenar en un Array todos los servicios
      const datos = []
      atencionesServicios.forEach(item => {
        datos.push(item.servicios)
      })
      serviciosInter.value = datos.flat()
      // Para Formulario de Atencin
      atencionData.value.paciente_id = editedItem.value.paciente_id
      atencionData.value.servidor_publico = editedItem.value.servidor_publico_id
      atencionData.value.triaje_id = editedItem.value.id

      dialog.value = true
    })

    const aprobarCita = () => {
      editedItem.value.estado = 'aprobado'
      editedItem.value.cita_id = editedItem.value.id
      axios.put(`citas/${editedItem.value.id}/triaje`, editedItem.value).then(() => {
        dialog.value = false
        toast({
          component: ToastificationContent,
          props: {
            title: 'Aprobacin exitosa',
            icon: 'AlertTriangleIcon',
            variant: 'success',
          },
        })
        emit('refresh-data-citas-table')
      }).catch(error => {
        toast({
          component: ToastificationContent,
          props: {
            title: 'Error!',
            icon: 'EditIcon',
            text: `${error.response.data.error}`,
            variant: 'danger',
          },
        })
      })
    }

    const crearFUACitas = item => {
      console.log(triajeData.value)
      if (triajeData.value.estado === 'pendiente') {
        toast({
          component: ToastificationContent,
          props: {
            title: 'Error!',
            icon: 'EditIcon',
            text: 'Primero debes realizar el triaje.',
            variant: 'danger',
          },
        })
        return
      }
      if (item.pivot.estado === 'por aprobar') {
        atencionData.value.servicio_id = item.id
        axios.put(`triajes/${triajeData.value.id}/crear-fua-citas`, atencionData.value)
          .then(response => {
            console.log(response.data)
            serviciosCitas.value = response.data.data
            emit('refresh-data-citas-table')
            toast({
              component: ToastificationContent,
              props: {
                title: 'FUA creado exitosamente',
                icon: 'AlertTriangleIcon',
                variant: 'success',
              },
            })
          })
      } else {
        toast({
          component: ToastificationContent,
          props: {
            title: 'Aviso!',
            icon: 'EditIcon',
            text: 'Ya tiene FUA!',
            variant: 'warning',
          },
        })
      }
    }

    const crearFUATriaje = item => {
      console.log(triajeData.value)
      console.log('Tengo triaje id', triajeData.value.id)
      if (triajeData.value.estado === 'pendiente') {
        toast({
          component: ToastificationContent,
          props: {
            title: 'Error!',
            icon: 'EditIcon',
            text: 'Primero debes realizar el triaje.',
            variant: 'danger',
          },
        })
        return
      }
      if (item.pivot.estado === 'por aprobar') {
        atencionData.value.servicio_id = item.id
        axios.put(`triajes/${triajeData.value.id}/crear-fua-triaje`, atencionData.value)
          .then(response => {
            console.log(response.data)
            serviciosTriaje.value = response.data.data
            emit('refresh-data-citas-table')
            toast({
              component: ToastificationContent,
              props: {
                title: 'FUA creado exitosamente',
                icon: 'AlertTriangleIcon',
                variant: 'success',
              },
            })
          })
      } else {
        toast({
          component: ToastificationContent,
          props: {
            title: 'Aviso!',
            icon: 'EditIcon',
            text: 'Ya tiene FUA!',
            variant: 'warning',
          },
        })
      }
    }
    const crearFUAInterconsulta = item => {
      if (triajeData.value.estado === 'pendiente') {
        toast({
          component: ToastificationContent,
          props: {
            title: 'Error!',
            icon: 'EditIcon',
            text: 'Primero debes realizar el triaje.',
            variant: 'danger',
          },
        })
        return
      }
      if (item.pivot.estado === 'por aprobar') {
        atencionData.value.triaje_id = triajeData.value.id
        atencionData.value.servicio_id = item.id
        axios.put(`atenciones/${item.pivot.model_id}/crear-fua-interconsulta`, atencionData.value)
          .then(response => {
            // Obtenemos todas las atenciones que al menos tengan un servicio asignado que seria una interconsulta
            const atencionesServicios = response.data.atencion.filter(i => i.servicios.length > 0)

            // Debemos almacenar en un Array todos los servicios
            const data = []
            atencionesServicios.forEach(i => {
              data.push(i.servicios)
            })

            serviciosInter.value = data.flat()

            emit('refresh-data-citas-table')
            toast({
              component: ToastificationContent,
              props: {
                title: 'FUA creado exitosamente',
                icon: 'AlertTriangleIcon',
                variant: 'success',
              },
            })
          })
      } else {
        toast({
          component: ToastificationContent,
          props: {
            title: 'Aviso!',
            icon: 'EditIcon',
            text: 'Ya tiene FUA!',
            variant: 'warning',
          },
        })
      }
    }
    const agregarServicios = () => {
      store.dispatch('triajes/addServicioTriaje', triajeData.value)
        .then(response => {
          serviciosTriaje.value = response.data.servicios
          emit('refresh-data-citas-table')
        })
    }

    const onSubmit = () => {
      if (editedIndex.value > -1) {
      // vamos a editar
        console.log('Vamos a editar')
        store.dispatch('triajes/updateCita', triajeData.value)
          .then(() => {
          // Clean form, Close Modal y Refresh Table
            toast({
              component: ToastificationContent,
              props: {
                title: 'Actualizacin exitosa',
                icon: 'AlertTriangleIcon',
                variant: 'success',
              },
            })
            emit('refresh-data-citas-table')
          })
          .catch(error => {
            console.log(error)
          })
      } else {
        console.log('Vamos a un nuevo registro')
        store.dispatch('triajes/addCita', triajeData.value)
          .then(() => {
          // Clean form, Close Modal y Refresh Table
            toast({
              component: ToastificationContent,
              props: {
                title: 'Registro exitoso!',
                icon: 'AlertTriangleIcon',
                variant: 'success',
              },
            })
            dialog.value = false
            emit('refresh-data-citas-table')
          })
      }
    }

    return {
      triajeData,
      resetuserData,
      onSubmit,
      close,
      dialog,
      formTitle,
      aprobarCita,
      atencionData,
      serviciosCitas,
      serviciosInter,
      serviciosTriaje,
      agregarServicios,
      crearFUACitas,
      crearFUATriaje,
      crearFUAInterconsulta,
    }
  },

}
</script>

<style lang="scss">
@import '@core/scss/vue/libs/vue-flatpicker.scss';
@import '@core/scss/vue/libs/vue-select.scss';
</style>
